.Toolbar{
    position: fixed;
    width:100%;
    background-color: rgb(255, 147, 71);
    height: 50px;
    z-index: 1;
}
.ToolbarLogo{
    display: flex;
}
.ToolbarLogo a{
    color:black;
    text-decoration: none;
    margin-top: auto;
    margin-bottom: auto;
}
.ToolbarNavigation{
    display: flex;
    height: 100%;
    width: 100%;
}
.ToolbarNavigationItems{
    display: flex;
    width:30%;
}
.ToolbarNavigationItems a{
    color:black;
    text-decoration: none;
    border-radius: 50px;
    margin-top: auto;
    margin-bottom: auto;
    min-width:70px;
    text-align: center;
}
.ToolbarNavigationItems ul{
    list-style:none;
    margin:0;
    padding:0;
    display: flex;
    width:100%;
    display: flex;
    justify-content: space-evenly;
}
.ToolbarNavigationItems li{
    display: flex;
    transition: background-color 1.2s;
    border-radius: 20px;
    min-width:70px;
    height: 70%;
    margin-top: auto;
    margin-bottom: auto;
}
.ToolbarNavigationItems li:hover,
.ToolbarNavigationItems li:active{
    background-color: rgb(255, 203, 166);
}
.ToolbarNavigationItems li{
    padding: 0 0.5rem;
}
.Spacer{
    flex:1;
}
#DrawerToggleButton{
    display: flex;
}
@media (max-width:768px){
    .ToolbarNavigationItems{
        display: none;
    }
}
@media (min-width:769px){
    #DrawerToggleButton{
        display: none;
    }
    .ToolbarLogo{
        margin-left: 20px;
    }
}