.SlideDrawer{
    height:100%;
    background-color: rgb(255, 127, 85);
    box-shadow:1px 0px 7px rgba(0,0,0,0.5);
    position:fixed;
    top:0;
    left:0;
    width:70%;
    max-width:400px;
    z-index:200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}
.SlideDrawer ul{
    height: 100%;
    width:100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content:left;
    width:90%;
}
.SlideDrawer a{
    color:black;
    text-decoration: none;
    font-size: 27px;
    height: fit-content;
    margin-top: auto;
    margin-bottom: auto;
    -webkit-transition:color 1.2s;
    transition:color 1.2s;
}
.SlideDrawer a:hover,
.SlideDrawer a:active{
    color:white;
}
.SlideDrawer li{
    display: flex;
    height: 15%;
    justify-content: center;
    margin-top: 10%;
    margin-right: 1%;
    margin-left: -11%;
    -webkit-transition:background-color 1.2s;
    transition:background-color 1.2s;
    border-radius: 50px;
    background-color: rgb(255, 145, 108);
}
.SlideDrawer li:hover,
.SlideDrower li:active{
    background-color: darkviolet;
}
.SlideDrawer.open{
    transform:translateX(0);
}
@media (min-width:769px){
    .SlideDrawer{
        display: none;
    }
}