.ToggleButton{
    height:24px;
    width:30px;
    background: transparent;
    border:none;
    cursor:pointer;
    display: flex;
    flex-direction: column;
    padding:0;
    box-sizing: border-box;
    justify-content: space-around;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: auto;
    margin-bottom: auto;
}
.ToggleButton:focus{
    outline:none;
}
.ToggleButtonLine{
    width:30px;
    height: 2.4px;
    background-color: white;
    border-radius: 50px;
}